export const objects = () => {
// –°–æ–∑–¥–∞—Ç—å –æ–±—ä–µ–∫—Ç, –æ–ø–∏—Å—ã–≤–∞—é—â–∏–π –∞–≤—Ç–æ–º–æ–±–∏–ª—å (–ø—Ä–æ–∏–∑–≤–æ–¥–∏—Ç–µ–ª—å, –º–æ–¥–µ–ª—å, –≥–æ–¥ –≤—ã–ø—É—Å–∫–∞, —Å—Ä–µ–¥–Ω—è—è —Å–∫–æ—Ä–æ—Å—Ç—å), –∏ —Å–ª–µ–¥—É—é—â–∏–µ —Ñ—É–Ω–∫—Ü–∏–∏ –¥–ª—è —Ä–∞–±–æ—Ç—ã —Å —ç—Ç–∏–º –æ–±—ä–µ–∫—Ç–æ–º:
//–§—É–Ω–∫—Ü–∏—è –¥–ª—è –≤—ã–≤–æ–¥–∞ –Ω–∞ —ç–∫—Ä–∞–Ω –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–∏ –æ–± –∞–≤—Ç–æ–º–æ–±–∏–ª–µ;
//–§—É–Ω–∫—Ü–∏—è –¥–ª—è –ø–æ–¥—Å—á–µ—Ç–∞ –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ–≥–æ –≤—Ä–µ–º–µ–Ω–∏ –¥–ª—è –ø—Ä–µ–æ–¥–æ–ª–µ–Ω–∏—è –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–≥–æ —Ä–∞—Å—Å—Ç–æ—è–Ω–∏—è —Å–æ —Å—Ä–µ–¥–Ω–µ–π —Å–∫–æ—Ä–æ—Å—Ç—å—é. –£—á—Ç–∏—Ç–µ, —á—Ç–æ —á–µ—Ä–µ–∑ –∫–∞–∂–¥—ã–µ 4 —á–∞—Å–∞ –¥–æ—Ä–æ–≥–∏ –≤–æ–¥–∏—Ç–µ–ª—é –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ –¥–µ–ª–∞—Ç—å –ø–µ—Ä–µ—Ä—ã–≤ –Ω–∞ 1 —á–∞—Å.
 
    document.getElementById("car").onclick = function car() {
        const manufacturer = prompt(`–í–≤–µ–¥–∏—Ç–µ –ø—Ä–æ–∏–∑–≤–æ–¥–∏—Ç–µ–ª—è –∞–≤—Ç–æ–º–æ–±–∏–ª—è`, ``);
        const model = prompt(`–í–≤–µ–¥–∏—Ç–µ –º–æ–¥–µ–ª—å –∞–≤—Ç–æ–º–æ–±–∏–ª—è`, ``);
        const yearOfManufacture = +prompt(`–í–≤–µ–¥–∏—Ç–µ –≥–æ–¥ –≤—ã–ø—É—Å–∫–∞ –∞–≤—Ç–æ–º–æ–±–∏–ª—è`, 2019);
        const averageSpeed = +prompt(`–í–≤–µ–¥–∏—Ç–µ —Å—Ä–µ–¥–Ω—é—é —Å–∫–æ—Ä–æ—Å—Ç—å –∞–≤—Ç–æ–º–æ–±–∏–ª—è`, 200);
        const distance = +prompt(`–í–≤–µ–¥–∏—Ç–µ —Ä–∞—Å—Å—Ç–æ—è–Ω–∏–µ, –∫–æ—Ç–æ—Ä–æ–µ –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ –ø—Ä–µ–æ–¥–æ–ª–µ—Ç—å`, 500);
        let distanceTime = distance/averageSpeed ;
        if(distanceTime >4) {
            distanceTime = distanceTime + Math.floor(distanceTime / 4);
          };
        const showCarInformation ={
        manufacturer: `${manufacturer}`,
        model: `${model}`,
        yearOfManufacture: `${yearOfManufacture}`,
        averageSpeed: `${averageSpeed}`,
              }
      alert(`–ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –æ–± –∞–≤—Ç–æ–º–æ–±–∏–ª–µ: –ø—Ä–æ–∏–∑–≤–æ–¥–∏—Ç–µ–ª—å - ${showCarInformation.manufacturer} ;
      –º–æ–¥–µ–ª—å - ${showCarInformation.model};
      –≥–æ–¥ –≤—ã–ø—É—Å–∫–∞ -  ${showCarInformation.yearOfManufacture} ;
      —Å—Ä–µ–¥–Ω—è—è —Å–∫–æ—Ä–æ—Å—Ç—å - ${showCarInformation.averageSpeed}.
      –î–ª—è –ø—Ä–µ–æ–¥–æ–ª–µ–Ω–∏—è –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–≥–æ —Ä–∞—Å—Å—Ç–æ—è–Ω–∏—è —Å–æ —Å—Ä–µ–¥–Ω–µ–π —Å–∫–æ—Ä–æ—Å—Ç—å—é –ø–æ–Ω–∞–¥–æ–±–∏—Ç—Å—è ${distanceTime} —á–∞—Å–æ–≤.`)
            };
  

// –°–æ–∑–¥–∞—Ç—å –æ–±—ä–µ–∫—Ç, —Ö—Ä–∞–Ω—è—â–∏–π –≤ —Å–µ–±–µ –æ—Ç–¥–µ–ª—å–Ω–æ —á–∏—Å–ª–∏—Ç–µ–ª—å –∏ –∑–Ω–∞–º–µ–Ω–∞—Ç–µ–ª—å –¥—Ä–æ–±–∏, –∏ —Å–ª–µ–¥—É—é—â–∏–µ —Ñ—É–Ω–∫—Ü–∏–∏ –¥–ª—è —Ä–∞–±–æ—Ç—ã —Å —ç—Ç–∏–º –æ–±—ä–µ–∫—Ç–æ–º: <br>
//–§—É–Ω–∫—Ü–∏—è —Å–ª–æ–∂–µ–Ω–∏—è 2-—Ö –æ–±—ä–µ–∫—Ç–æ–≤-–¥—Ä–æ–±–µ–π;
// –§—É–Ω–∫—Ü–∏—è –≤—ã—á–∏—Ç–∞–Ω–∏—è 2-—Ö –æ–±—ä–µ–∫—Ç–æ–≤-–¥—Ä–æ–±–µ–π;
//–§—É–Ω–∫—Ü–∏—è —É–º–Ω–æ–∂–µ–Ω–∏—è 2-—Ö –æ–±—ä–µ–∫—Ç–æ–≤-–¥—Ä–æ–±–µ–π;
// –§—É–Ω–∫—Ü–∏—è –¥–µ–ª–µ–Ω–∏—è 2-—Ö –æ–±—ä–µ–∫—Ç–æ–≤-–¥—Ä–æ–±–µ–π;
//–§—É–Ω–∫—Ü–∏—è —Å–æ–∫—Ä–∞—â–µ–Ω–∏—è –æ–±—ä–µ–∫—Ç–∞-–¥—Ä–æ–±–∏.        
 document.getElementById("WorkingWithFractions").onclick = function WorkingWithFractions() {
    const firstNumerator = +prompt(`–í–≤–µ–¥–∏—Ç–µ –ø–µ—Ä–≤—ã–π —á–∏—Å–ª–∏—Ç–µ–ª—å`, 2);
    const firstDenominator = +prompt(`–í–≤–µ–¥–∏—Ç–µ –ø–µ—Ä–≤—ã–π –∑–Ω–∞–º–µ–Ω–∞—Ç–µ–ª—å`, 4);
    const secondNumerator = +prompt(`–í–≤–µ–¥–∏—Ç–µ –≤—Ç–æ—Ä–æ–π —á–∏—Å–ª–∏—Ç–µ–ª—å`, 4);
    const secondDenominator = +prompt(`–í–≤–µ–¥–∏—Ç–µ –≤—Ç–æ—Ä–æ–π –∑–Ω–∞–º–µ–Ω–∞—Ç–µ–ª—å`, 8);

    let addingResult =(firstNumerator/firstDenominator)+(secondNumerator/secondDenominator);
let subtractionResult = (firstNumerator/firstDenominator)-(secondNumerator/secondDenominator);
let multiplicationResult = (firstNumerator/firstDenominator)*(secondNumerator/secondDenominator);
let divisionResult  = (firstNumerator/firstDenominator)/(secondNumerator/secondDenominator);

alert(`–†–µ–∑—É–ª—å—Ç–∞—Ç —Å–ª–æ–∂–µ–Ω–∏—è:${addingResult}; –†–µ–∑—É–ª—å—Ç–∞—Ç –≤—ã—á–∏—Ç–∞–Ω–∏—è: ${subtractionResult}; 
–†–µ–∑—É–ª—å—Ç–∞—Ç —É–º–Ω–æ–∂–µ–Ω–∏—è: ${multiplicationResult}; –†–µ–∑—É–ª—å—Ç–∞—Ç –¥–µ–ª–µ–Ω–∏—è: ${divisionResult} . `);
        };


//–°–æ–∑–¥–∞—Ç—å –æ–±—ä–µ–∫—Ç, –æ–ø–∏—Å—ã–≤–∞—é—â–∏–π –≤—Ä–µ–º—è (—á–∞—Å—ã, –º–∏–Ω—É—Ç—ã, —Å–µ–∫—É–Ω–¥—ã), –∏ —Å–ª–µ–¥—É—é—â–∏–µ —Ñ—É–Ω–∫—Ü–∏–∏ –¥–ª—è —Ä–∞–±–æ—Ç—ã —Å —ç—Ç–∏–º –æ–±—ä–µ–∫—Ç–æ–º: 
//–§—É–Ω–∫—Ü–∏—è –≤—ã–≤–æ–¥–∞ –≤—Ä–µ–º–µ–Ω–∏ –Ω–∞ —ç–∫—Ä–∞–Ω;
//–§—É–Ω–∫—Ü–∏—è –∏–∑–º–µ–Ω–µ–Ω–∏—è –≤—Ä–µ–º–µ–Ω–∏ –Ω–∞ –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å–µ–∫—É–Ω–¥;
//–§—É–Ω–∫—Ü–∏—è –∏–∑–º–µ–Ω–µ–Ω–∏—è –≤—Ä–µ–º–µ–Ω–∏ –Ω–∞ –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –º–∏–Ω—É—Ç;
//–§—É–Ω–∫—Ü–∏—è –∏–∑–º–µ–Ω–µ–Ω–∏—è –≤—Ä–µ–º–µ–Ω–∏ –Ω–∞ –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —á–∞—Å–æ–≤. 
document.getElementById("WorkingWithTime").onclick = function WorkingWithTime() {
    let now = new Date();
    alert( `–°–µ–π—á–∞—Å ${now} `); 

    const seconds = +prompt(`–í–≤–µ–¥–∏—Ç–µ –∫–æ–ª–ª–∏—á–µ—Å—Ç–≤–æ —Å–µ–∫—É–Ω–¥ –¥–ª—è –∏–∑–º–µ–Ω–µ–Ω–∏—è –≤—Ä–µ–º–µ–Ω–∏`, 2);
    const minutes = +prompt(`–í–≤–µ–¥–∏—Ç–µ –∫–æ–ª–ª–∏—á–µ—Å—Ç–≤–æ –º–∏–Ω—É—Ç –¥–ª—è –∏–∑–º–µ–Ω–µ–Ω–∏—è –≤—Ä–µ–º–µ–Ω–∏`, 4);
    const hours = +prompt(`–í–≤–µ–¥–∏—Ç–µ –∫–æ–ª–ª–∏—á–µ—Å—Ç–≤–æ —á–∞—Å–æ–≤ –¥–ª—è –∏–∑–º–µ–Ω–µ–Ω–∏—è –≤—Ä–µ–º–µ–Ω–∏`, 4);
    now.setSeconds(now.getSeconds() + seconds);
    now.setMinutes(now.getMinutes() + minutes);
    now.setHours(now.getHours() + hours);
alert(`–ï—Å–ª–∏ –ø—Ä–∏–±–∞–≤–∏—Ç—å –≤–≤–µ–¥–µ–Ω–Ω—ã–µ —á–∞—Å—ã, –º–∏–Ω—É—Ç—ã –∏ —Å–µ–∫—É–Ω–¥—ã –≤—Ä–µ–º—è –±—É–¥–µ—Ç  ${now}`)
 };
}
